.firstHeader {
  display: none;
}

.mainMenu {
  position: fixed;
  display: flex;
  flex-direction: row;
  border-radius: none;
  justify-content: center;
  width: 100%;
  background: black;
  z-index: 5;
}

.mainMenu__item {
  padding: 20px;
  color: white;
  cursor: pointer;
  transition: all .3s ease-in-out;
  letter-spacing: 1px;
  border-bottom: 2px solid black;
}

.mainMenu__item:hover {
  background: #BF7E04;
  border-bottom: 2px solid #BF7E04;
}

.mainMenu__item.active {
  background: rgba(191, 126, 4, 0.5);
  border-bottom: 2px solid #BF7E04;
  cursor: auto;
}

.mainMenu__item--CV {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 5px;
  font-weight: 300;
  color: white;
  padding: 10px;
  border: 10px solid black;
  background: rgba(191, 126, 4, 0.5);
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.mainMenu__item--CV:hover {
  background: #BF7E04;
}

.menuCircles {
  display: none;
  position: fixed;
  right: 20px;
  top: 40%;
}

.circle {
  padding: 5px;
  border: 2px solid black;
  margin: 10px;
  border-radius: 50%;
  transition: all .2s ease-in-out;
  cursor: pointer;
}

.circle:hover {
  background: black;
}

.circle.active {
  background: black;
  box-shadow: 0 0 0 10px bgHover_2;
}

