.mainHeader {
  height: 110vh;
  width: 100vw;
  background: url(/portfolio/static/media/header.cb78d418.jpg) center;
  background-size: cover;
  background-attachment: fixed;
}

.mainHeader__panels {
  position: absolute;
  display: flex;
  justify-content: space-around;
  top: 80vh;
  width: 70vw;
  margin: auto 15vw;
}

.mainHeader__me {
  position: relative;
  width: 35vw;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.me__myName {
  font-family: 'Fredericka the Great', cursive;
  padding: 20px;
  display: block;
  text-align: center;
  width: 35vw;
  background: rgba(0, 0, 0, 0.3);
  background: rgba(255, 255, 255, 0.7);
  color: white;
  color: black;
  border: 1px solid white;
  font-weight: 300;
  font-size: 60px;
  letter-spacing: 3px;
}

.me__prof {
  border: 1px solid white;
  color: white;
  background: black;
  display: flex;
  font-weight: 300;
  width: 30vw;
  justify-content: space-around;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.me__prof--dev, .me__prof--data, .me__prof--math {
  display: block;
  font-size: 10px;
  margin: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.panel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 20vw;
  color: white;
  background: black;
  padding: 20px;
  border-radius: 10px;
  transition: all .4s ease-in-out;
  cursor: pointer;
}

.panel:hover {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
